@import url(https://fonts.googleapis.com/css2?family=Bubblegum+Sans&display=swap);
.rainbow-animation {
  background: linear-gradient(225deg, #fe7474 10%, #ffb253 27%, #a1ff7e 43%, #78bbff 66%, #ff63b6 80%, #fe7474 100%);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  -webkit-animation: rainbow_animation 20s linear infinite;
          animation: rainbow_animation 20s linear infinite;
  -webkit-animation-duration: 20s;
          animation-duration: 20s;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-direction: normal;
          animation-direction: normal;
  -webkit-animation-fill-mode: none;
          animation-fill-mode: none;
  -webkit-animation-play-state: running;
          animation-play-state: running;
  -webkit-animation-name: rainbow_animation;
          animation-name: rainbow_animation;
  background-size: 400% auto; }

@-webkit-keyframes rainbow_animation {
  to {
    background-position: 400% center; } }

@keyframes rainbow_animation {
  to {
    background-position: 400% center; } }

